<template>
  <div class="addblog">
    <h2>{{msg}}</h2>
    <div class="add" v-if="!submited">
      <form>
        <div>
          <p><label for="blogtitle">标题</label></p>
          <input id="blogtitle" type="text" v-model="blog.title">
        </div>
        <div>
          <p>
            <label for="blogcontent">博客内容</label>
          </p>
          <textarea id="blogcontent"  v-model="blog.content" rows="10">

          </textarea>
        </div>
        <div id="blogcategory">
          <p>
            <label>博客分类</label>
          </p>
          <label for="vuejs">Vue.js</label>
          <input id="vuejs" type="checkbox" value="Vue.js" v-model="blog.category">
          <label for="nodejs">Node.js</label>
          <input id="nodejs" type="checkbox" value="Node.js" v-model="blog.category">
          <label for="reactjs">React.js</label>
          <input id="reactjs" type="checkbox" value="React.js" v-model="blog.category">
          <label for="angular4">Angular4</label>
          <input id="angular4" type="checkbox" value="Angular4" v-model="blog.category">
        </div>
        <div>
          <p>
            <label for="blogauthor">作者</label>
          </p>
          <select name="blogauthor" id="blogauthor" v-model="blog.author">
            <option v-for="author in authors" >{{author}}</option>
          </select>
        </div>
        <div>
          <button v-on:click.prevent="posts">添加博客</button>
        </div>

      </form>
    </div>
    <hr>
    <p class="info" v-if="submited">添加博客成功</p>
    <div class="preview" v-if="submited">
      <h3>当前添加博客预览</h3>
      <p>标题：{{blog.title}}</p>
      <p>博客内容：{{blog.content}}</p>
      <p>分类：{{blog.category}}</p>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>


<script>
  export default {
      name: "AddBlog",
      data(){
          return {
              msg: "添加博客",
              blog: {
                  title: "",
                  content: "",
                  category: [],
                  author: ""
              },
              authors: ["zdong", "zzx", "zyf"],
              submited: false
          }
      },
      methods:{
          posts(){
              // 1、通过后端获取，自己写接口
              // "http://jsonplaceholder.typicode.com/posts"
              /*this.$http.post("http://localhost:3000/blogs",
                  {
                      title: this.blog.title,
                      body: this.blog.content,
                      category: this.blog.category,
                      author: this.blog.author,
                      userId: 1
                  }).then(function(res){
                      this.submited = true
                      console.log(res.body)
              })*/

              //2、Bmob云数据库服务端
              var myComponent = this
              const query = Bmob.Query('blogs')
              query.set("title", this.blog.title)
              query.set("body", this.blog.content)
              query.set("category", this.blog.category)
              query.set("author", this.blog.author)
              query.save().then(function(res){
                  console.log(res)
                  myComponent.$data.submited = true
              })
                  .catch(function(err){
                  console.log(err)
              })
          }
      }
  }
</script>


<style scoped>

  .addblog{
    max-width: 600px;
    margin:10px auto;
    /*border: 1px solid #42b983;*/
  }
  .addblog h2{
    text-align: center;
  }
  .addblog p label{
    font-weight: bold;
  }
  input[type="text"], textarea, select{
    box-sizing: border-box;
    display: inline-block;
    width: 100%;
    height: 30px;
    border: 1px solid #ccc;
  }
  textarea{
    height: 100px;
  }

  button{
    margin: 20px 5px;
    padding: 10px 20px;
    background: #f42323;
    border-radius: 5px;
    color: #fff;
  }
  p.info{
    font-size: 22px;
    color: #4444ff;
    font-weight: bold;
  }



</style>



