// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Resource from "vue-resource"
import router from "./router"
import App from './App'
import Bmob from 'hydrogen-js-sdk'



Vue.config.productionTip = false

Vue.use(Resource)

// 初始化Bmob后端云数据库(2.0.0后启用新的自定义API Key)
// 下面网址有说明
// https://bmob.github.io/hydrogen-js-sdk/#/?id=%e5%ae%89%e8%a3%85%e4%bd%bf%e7%94%a8
Bmob.initialize("ce8a9d3cf57717bc", "123456")


// 实现自定义指令(这个是全局的实现，在局部实现时，在组件导出部分增加directives，见ShowBlog.vue中)
// 1、无传值时的实现 参数为 字符串和对象
Vue.directive("rainbow", {
  bind(el, binding, vnode){
    el.style.color = "#" + Math.random().toString(16).slice(2, 8)
    //console.log(binding)
    //console.log(Math.random().toString(16).slice(2,8))
  }
})

// 2、有参数及有变量
Vue.directive("theme", {
  bind(el, binding, vnode){
    console.log(binding)
    if (binding.value == "wide"){
      el.style.maxWidth = "1200px"
    } else if (binding.value == "narrow"){
      el.style.maxWidth = "400px"
    } else if (binding.value == "normal"){
      el.style.maxWidth = "800px"
    }
    if (binding.arg == "column"){
      el.style.background ="#6677cc"
      el.style.padding = "20px"
    }
  }
})

// 自定义过滤器（这个是全局的实现，在局部实现时，在组件导出部分增加filters，见ShowBlog.vue中）
// 参数为 字符串和方法
Vue.filter("to-uppercase", function(value){
  return value.toUpperCase()
})

Vue.filter("snippet", function(value){
  if (value.length >= 50) {
    return value.slice(0, 50) + '...'
  } else
    return value
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
}).$mount('#app')



