<template>
<!-- 自定义指令 1、变量可为字符串如'wide'或'narrow'或'normal'（要加引号），可为对象，可为数组-->
<!--           2、绑定的参数在指令后，如column-->
  <div v-theme:column="'normal'" class="container">
<!-- v-rainbow 是未带变量及参数的自定义指令，在main.js中实现   -->
    <h2 v-rainbow>{{msg}}</h2>
    <hr>
    <div class="search">
      <input type="text" placeholder="搜索" v-model="search">
    </div>
    <div class="blogs">
      <div class="blog" v-for="blog in filterBlogs">  <!--blogs-->
        <router-link v-bind:to="'/singleblog/' + blog.objectId">
          <h3 v-rainbow>
            {{blog.title | to-uppercase | to-lowercase}}
          </h3>
        </router-link>

        <article v-raincolor>
          {{blog.body | snippet | detail}}
        </article>
      </div>
    </div>
  </div>
</template>


<script>
  export default {
      name: "ShowBlog",

      data(){
          return {
              msg: "我的博客主页",
              search: "",
              blogs: []
          }
      },

      created(){
          //1、通过后端获取，自己写接口
          // "http://jsonplaceholder.typicode.com/posts"
          // "http://localhost:3000/blogs"
          /*this.$http.get("http://localhost:3000/blogs")
              .then(function(res){
                  this.blogs = res.body.slice(0, 10)
                  console.log('blogs')
                  console.log(this.blogs)
              })
           */
         // //2、Bmob云数据库服务端
          var myComponent = this
          const query = Bmob.Query("blogs")

          query.get('').then(function(res){
              console.log(res.results)
              //console.log(myComponent.$data.blogs)
              myComponent.$data.blogs = res.results
          }).catch(function(err){
              console.log(err)
          })

      },

      computed: {
          filterBlogs: function(){
              return this.blogs.filter((blog) => {
                  //console.log(blog)
                  return blog.title.match(this.search)
              })
          }
      },

      filters: {
          "to-lowercase": function(value){
              return value.toLowerCase()
          },
          "detail": function(value){
              return value + '[详细]'
          }
      },

      directives: {
          "raincolor": {
              bind(el, binding, vnode){
                  el.style.color = "#" + Math.random().toString(16).slice(2, 8)
                  //console.log(el)
              }
          }
      }
  }
</script>


<style scoped>

  .container{
    max-width: 800px;
    margin: 0 auto;
    background: #f2f2f2;
  }

  .blog{
    box-sizing: border-box;
    padding: 20px;
    margin: 20px 0;
    background: #ededed;
  }

  a{
    text-decoration: none;
  }


</style>



