<template>
  <div class="singleblog">
    {{msg}}
    <h3 class="title">
      {{blog.title}}
    </h3>
    <div class="info">
      <p>{{blog.author}}</p>
      <p>{{blog.category.toString()}}</p>
    </div>
    <p class="content">
      {{blog.body}}
    </p>
  </div>
</template>

<script>
  export default {
      name: "SingleBlog",
      data() {
          return {
              msg: "博客详情页SingleBlog",
              id: null,
              blog: {
                  title: "",
                  body: "",
                  author: "",
                  category: []
              }
          }
      },
      created(){
          // 1、通过后端获取，自己写接口
          // "http://jsonplaceholder.typicode.com/posts/"
          /*this.id = this.$route.params.id
          this.$http.get("http://localhost:3000/blogs/" + this.id )
              .then(function(res){
                  console.log(res.body)
                  this.blog = res.body
              })*/

          // //2、Bmob云数据库服务端
          this.id = this.$route.params.id
          var myComponent = this
          const query = Bmob.Query("blogs")
          query.get(this.id).then(function(res){
              //console.log(res)
              myComponent.$data.blog = res
              //console.log(myComponent.$data.blog)
          }).catch(function(err){
              console.log(err)
          })

      }
  }
</script>


<style scoped>
  .singleblog{
    margin: 20px auto;
    max-width: 800px;
    padding-bottom: 40px;
    background: #ddd;
  }

  .title{
    margin: 20px 0;
    padding: 10px 20px;
    text-align: center;
    color: #444;
  }

  p.content{
    padding: 10px 20px;
    font-size: 18px;
    line-height: 1.5em;
    color: #333;
    text-align: left;
  }
</style>



